export default function Footer() {
    const currentYear = new Date().getFullYear();

    return (
        <footer className="mt-24 pb-16">
            <div className="container-minimal border-t border-[var(--border)] pt-8">
                <div className="flex flex-col md:flex-row justify-between items-center gap-4 text-sm text-[var(--foreground-secondary)] font-sans">
                    <div>
                        © {currentYear} happy-haru.
                    </div>

                    <div className="flex items-center space-x-6">
                        <a href="https://github.com/happy-haru" target="_blank" rel="noopener noreferrer" className="hover:text-[var(--primary)] transition-colors">
                            GitHub
                        </a>
                        <a href="/rss.xml" className="hover:text-[var(--primary)] transition-colors">RSS</a>
                    </div>
                </div>

                <p className="mt-4 text-center md:text-left text-xs text-[var(--foreground-secondary)/60] font-mono">
                    "데이터 안에 답이 있다면, 저는 그걸 꺼내오는 사람입니다."
                </p>
            </div>
        </footer>
    );
}
